body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  text-align: center;
}

.page-content{
  max-width:  800px;
  min-width:  450px;
  margin: 0 auto;
}

.inner-bar{
    max-width:  450px;
    min-width: 430px;
    margin: 0 auto;
}

.active-menu{
    background-color: green;
}

.nav-bar{
    background-color:   #333;
}
.nav-bar li{
    /* display: inline;*/
    float: left;
}
.nav-bar ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}
.nav-bar li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }

  .nav-bar li a:hover {
    background-color: green;
  }

  .page-content{
      margin-top: 50px;
  }

  /*Datos*/
  .izq{
      float: left;
  }
  .izq img{
      border-radius: 50%;
  }
  .der{
      float: left;
      margin-left: 20%;
  }
  .name{
      font-size: 20px;
      font-weight: bold;
  }
  .tel{
      font-style: italic;
      -webkit-text-decoration-line: underline;
              text-decoration-line: underline;
  }
  .email{
      font-style: italic;
      -webkit-text-decoration-line: underline;
              text-decoration-line: underline;
      color: lightskyblue;
  }
  .location{
      font-size: 18px;
      font-weight: bold;
  }
