.inner-bar{
    max-width:  450px;
    min-width: 430px;
    margin: 0 auto;
}

.active-menu{
    background-color: green;
}

.nav-bar{
    background-color:   #333;
}
.nav-bar li{
    /* display: inline;*/
    float: left;
}
.nav-bar ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}
.nav-bar li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }

  .nav-bar li a:hover {
    background-color: green;
  }

  .page-content{
      margin-top: 50px;
  }

  /*Datos*/
  .izq{
      float: left;
  }
  .izq img{
      border-radius: 50%;
  }
  .der{
      float: left;
      margin-left: 20%;
  }
  .name{
      font-size: 20px;
      font-weight: bold;
  }
  .tel{
      font-style: italic;
      text-decoration-line: underline;
  }
  .email{
      font-style: italic;
      text-decoration-line: underline;
      color: lightskyblue;
  }
  .location{
      font-size: 18px;
      font-weight: bold;
  }